import React from 'react';
import { useResult } from './ResultContext';

interface Props {
}


export default function Result(props: Props) {

  const { result } = useResult();

  if (!result) {
    return <p></p>;
  }


  return (
    <div>
      <h2>Result</h2>
      {/*  */}
      <p>: {result.elapsedTime} ms</p>
      <p>: {result.elapsedTime / 1000} s</p>
      <p>: {result.errorCount}</p>
    </div>
  );
};