import React, { useEffect, useState } from 'react';
import { useResult } from './ResultContext';

interface Props {
  onTaskCompleted: (elapsedTime: number, errorCount: number) => void;
}

const TaskComponent: React.FC<Props> = ({ onTaskCompleted }) => {
  const [startTime, setStartTime] = useState<number | null>(null);
  const [errorCount, setErrorCount] = useState(0);
  const { setResult } = useResult();

  // 
  useEffect(() => {
    setStartTime(Date.now());
  }, []);

  // 
  const handleError = () => {
    setErrorCount((prevErrorCount) => prevErrorCount + 1);
  };

  // 
  const handleTaskCompleted = () => {
    if (startTime) {
      const elapsedTime = Date.now() - startTime;
      onTaskCompleted(elapsedTime, errorCount);
      setResult({ elapsedTime, errorCount });
    }
  };

  return (
    <div>
      {/* UI */}
      {/* handleErrorhandleTaskCompleted */}
      <p>UI</p>
      <p></p>
      <button></button>
      <button></button>
      <button onClick={handleError}></button>
      <button onClick={handleError}></button>
      <button></button>
      <button onClick={handleError}></button>
      <button onClick={handleError}></button>
      <br></br>
      <button onClick={handleTaskCompleted}></button>
    </div>
  );
};

export default TaskComponent;